import { Component, OnInit, ViewChild, ElementRef, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrManager } from 'ng6-toastr-notifications';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { AppService } from 'src/app/app.service';
import { SocketService } from 'src/app/socket.service';
import { TreetopComponent } from 'src/app/shared/treetop/treetop.component';
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {
  @HostListener('document:keydown', ['$event']) sendMessageUsingKeypress(event: KeyboardEvent) {
     
    console.log(event.keyCode);
     
    if (event.keyCode === 90 && event.ctrlKey) {  

      this.UndoAction();

    }
}
  public todolist:any=[];
  public authToken: any;
  public userInfo: any;
  public userId:any;
  public userList: any = [];
  public listName:any;
  public value:any;
  public undo:any;
  public disconnectedSocket: boolean;
  constructor(private router:Router,
    public toastr: ToastrManager,
    private appService:AppService,
    private SocketService:SocketService) { console.log('con');}
	@ViewChild(TreetopComponent,{static:false})
	private treetop:TreetopComponent;

  ngOnInit() {
    console.log('nginti');
    this.userId = Cookie.get("receiverId");
    this.authToken = Cookie.get('authToken');
    this.userInfo = this.appService.getUserInfoIntoLocalStorage();
    this.showUserTodoList();
    this.checkStatus();
    
    this.verifyUserConfirmation();
    this.getRequestFromAUser();
     
  }
  public checkStatus: any = () => {

    if (Cookie.get('authToken') === undefined || Cookie.get('authToken') === '' || Cookie.get('authToken') === null) {
      console.log('auth');
      this.router.navigate(['/']);

      return false;

    } else {

      return true;

    }

  } // end checkStatus
   

  public verifyUserConfirmation: any = () => {

    this.SocketService.verifUser()
      .subscribe((data) => {
        
        this.disconnectedSocket = false;
        console.log('verified');
        this.SocketService.setUser(this.authToken);
      });
    }

    //function to get notification for new friend request
  public getRequestFromAUser = ()=>{

    this.SocketService.getFriendRequestUserId(this.userInfo.userId)
    .subscribe((data)=>{
    this.toastr.successToastr(`${data} `);
});//end of getRequestFromAUser
  }
 
  
  
  public goToTodo():any{
    return this.router.navigate(['/todolist']);
  }
   public goToOnlineUser():any{
    return this.router.navigate(['/onlineuser']);
  }
  public showUserTodoList=():any=>{
      console.log('show');
     this.appService.showUserTodoList(this.userId).subscribe(
       (data)=>{
         for (let item of data) {
           this.todolist.push(item);
          }
        
        console.log(this.todolist);
       },
       (err)=>{
        this.toastr.errorToastr('No list to show create one');
       }
       )
       }
  public createTodoList=():any=>{
    if (!this.listName) {
      this.toastr.warningToastr("enter Listname");
    }else{
       
      let data = {
        userId:this.userId,
        listName: this.listName,
        value:this.listName,
      }
      this.appService.createTodoList(data).subscribe(

        (apiResponse: any) => {
           if(apiResponse.error){
            this.toastr.warningToastr(apiResponse.message);
           }else{
            this.toastr.successToastr("List Created");
            this.todolist.push(apiResponse);
            
        }
           
          
        },
        (error) => {
          this.toastr.errorToastr('Response not geting',error);
        }
      );
    }
  }

  public logout: any = () => {
    console.log("logout called")
    this.appService.logout()
      .subscribe((apiResponse) => {
  
        if (apiResponse.status === 200) {
          console.log("logout called")
          Cookie.delete('authToken');
  
          Cookie.delete('receiverId');
  
          Cookie.delete('receiverName');

      
          
          this.SocketService.exitSocket()
  
          this.router.navigate(['/']);
  
        } else {
          this.toastr.errorToastr(apiResponse.message)
  
        } // end condition
  
      }, (err) => {
        this.toastr.errorToastr('some error occured')
  
  
      });
  
  } 
  
  public UndoAction=():any=>{
	  console.log('undoAction');
	  this.appService.undoAction(this.userId).subscribe(
	  (data:any)=>{
		  if(data===null || data===undefined || data===""){
			  console.log('no undo action');
		  }else if(data.todo[0].top==true){
			  console.log('no item to delete');
		  }
		  else{
		  this.undo=data;
		  console.log( this.undo);
		  this.treetop.anyUndo(this.undo);
		  }
	  },
	  (error)=>{
		  console.log('error',error);
	  }
	  )
  }
}
