import { Component, OnInit, Input, Output, OnChanges, EventEmitter } from '@angular/core';
import { ToastrManager } from 'ng6-toastr-notifications';
import { AppService } from 'src/app/app.service';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { SocketService } from 'src/app/socket.service';
@Component({
  selector: 'app-treetop',
  templateUrl: './treetop.component.html',
  styleUrls: ['./treetop.component.css']
})
export class TreetopComponent implements OnInit {
  public item: string;
  public items: any = [];
  public checkboxValue:any=[];
  public userId: any;
  public userInfo:any
  @Input('list_id') list_id: any;
 
  public list: any;

   
  constructor(

    public toastr: ToastrManager,
    private appService: AppService,
    private socketService:SocketService
  ) { }

	
  ngOnInit() {
    console.log('ng');
    this.userInfo = this.appService.getUserInfoIntoLocalStorage();
    this.userId = Cookie.get("receiverId");
    console.log(this.list_id);
    this.getTodo(this.list_id);
	 
	

  }
  public addSubItem = (id, value,optional=0) => {
    if (!this.items) {
      this.toastr.warningToastr("first enter item");
    } else {
       

      console.log('addItem', this.list);
      let item = {
        userId: this.list[0].userId,
        listName: value,
        value: value,
        parentId: id,
		optional:optional
      }
      this.appService.addItem(item).subscribe(

        (apiResponse: any) => {
          if (apiResponse.error) {
            console.log('err');
            this.toastr.warningToastr(apiResponse.message);
          } else {
            this.toastr.successToastr("List Created");
		      	console.log('checked',this.list_id);
            this.getTodo(this.list_id);
          
          }


        },
        (error) => {
          this.toastr.errorToastr('Response not geting', error);
        }
      );

    }
  }


  public addItemUndo = (id, value,_id,optional=0) => {
     
      let item = {
        userId: this.list[0].userId,
		_id:_id,
        listName: value,
        value: value,
        parentId: id,
		optional:optional
      }
      this.appService.addItemUndo(item).subscribe(

        (apiResponse: any) => {
          if (apiResponse.error) {
            console.log('err');
            this.toastr.warningToastr(apiResponse.message);
          } else {
            this.toastr.successToastr("List undo");
			console.log('checked',this.list_id);
            this.getTodo(this.list_id);

          }


        },
        (error) => {
          this.toastr.errorToastr('Response not geting', error);
        }
      );

    
  }
  public getTodo = (id) => {
    let data = {

      parentId: id
    }

    this.appService.getTodo(data).subscribe(
      (apiResponse) => {
        this.list = apiResponse;
        console.log(this.list);
        for (let index = 0; index < this.list.length; index++) {
          this.items[index] = "";
		  this.checkboxValue[index]=(this.list[index].done=="1")?true:false;
        }
        console.log('checkboxValue',this.checkboxValue);
      },
      (err) => {
        console.log(err);
      }
    )
  }


  public DeleteItem=(_id,optional=0)=>{
	 this.appService.DeleteItem(_id,this.list[0].userId,optional).subscribe(

        (apiResponse: any) => {
          if (apiResponse.error) {
            console.log('err');
            this.toastr.warningToastr(apiResponse.message);
          } else {
            this.toastr.successToastr("List deleted");
			console.log('checked',this.list_id);
            this.getTodo(this.list_id);
			if(_id==this.list_id){
				location.reload();
			}

          }
		  },
        (error) => {
          this.toastr.errorToastr('Response not geting', error);
        }
      );
  }
  
  public editItem=(_id,value,optional=0)=>{
	 this.appService.editItem(_id,value,this.list[0].userId,optional).subscribe(

        (apiResponse:any) => {
          if (apiResponse.error) {
            console.log('err');
            this.toastr.warningToastr(apiResponse.message);
          } else {
            this.toastr.successToastr("Item Edited");
            this.getTodo(this.list_id);
			if(_id==this.list_id){
				location.reload();
			}

          }
		  },
        (error) => {
          this.toastr.errorToastr('Response not geting', error);
        }
      );
  }
  
  public changeCheckBox=(_id,value,optional=0)=>{
    
	  if(value==true){
		  value='1';
	  }else{
		  value='0'
	  }
	 this.appService.changeCheckBox(_id,value,this.list[0].userId,optional).subscribe(

        (apiResponse:any) => {
          if (apiResponse.error) {
            console.log('err');
            this.toastr.warningToastr(apiResponse.message);
          } else {
            this.toastr.successToastr("Item Edited");
            this.getTodo(this.list_id);
			if(_id==this.list_id){
				location.reload();
			}

          }
		  },
        (error) => {
          this.toastr.errorToastr('Response not geting', error);
        }
      );
  }

  public anyUndo=(undo)=>{
	  console.log('any undo');
	  if(undo==='' || undo===undefined || undo===null  ){
		  
		  console.log('no item in undo')
		   
	  }else  {
		  let optional=1;
		  console.log('app',undo)
		  if(undo.typeOfCrud==="ItemAdded"){
			  this.DeleteItem(undo.todo[0]._id,optional);
		  }else if(undo.typeOfCrud==="Itemdeleted"){
			   this.addItemUndo(undo.todo[0].parentId,undo.todo[0].value,undo.todo[0]._id,optional);
		  }else if(undo.typeOfCrud==="ItemEdited"){
			  this.editItem(undo.todo[0]._id,undo.todo[0].value,optional);
		  }
		  location.reload();
	  }
  }
}
