import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { RouterModule } from '@angular/router';
import {FormsModule,ReactiveFormsModule} from '@angular/forms';
import { OnlineuserComponent } from './onlineuser/onlineuser.component';
import { SharedModule } from '../shared/shared.module';
import { FriendlistComponent } from './friendlist/friendlist.component';
import { RecoverPaswordComponent } from './recover-pasword/recover-pasword.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NotfoundComponent } from './notfound/notfound.component';


@NgModule({
  declarations: [LoginComponent, SignupComponent, OnlineuserComponent, FriendlistComponent, RecoverPaswordComponent, NotfoundComponent],
  imports: [
    CommonModule,
    
    RouterModule.forChild([
      {path:'signup',component:SignupComponent},
      {path:'recoverpassword',component:RecoverPaswordComponent},
      {path:'onlineuser',component:OnlineuserComponent },
      {path:'friendlist',component:FriendlistComponent}
    ]),
    FormsModule,
    SharedModule,
    BrowserAnimationsModule,
  ]
})
export class UserModule { }
