import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';
import { Observable, observable } from 'rxjs';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { catchError,tap } from 'rxjs/operators';
 
 
import {HttpClient,HttpHeaders, HttpErrorResponse} from '@angular/common/http';
import {HttpResponse,HttpParams} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SocketService {

  private url='http://localhost:3000/';
  private socket;
  constructor(public http:HttpClient) {
    //connection being created
    this.socket=io(this.url);
   }
   //events to be listend
   public verifUser=()=>{
     console.log('ver');
     return Observable.create((observer)=>{
        this.socket.on('verifyUser',(data)=>{
          observer.next(data);
        });
     });
   }
   public onlineUserList=()=>{
     console.log('socket service')
     let observable=Observable.create((observer)=>{
        this.socket.on('online-user-list',(userList)=>{
            observer.next(userList);
        });
     });
     return observable;
   }
   public getFriendRequestUserId=(id)=>{
    return Observable.create((observer)=>{
      this.socket.on(id,(data)=>{
        observer.next(data);
      });
   });
  }

  
 

   public disconnectedSocket=()=>{
    let observable=Observable.create((observer)=>{
      this.socket.on('disconnect',()=>{
        observer.next();
      });
    });
    return observable;
   }

   //events to be emited
   public setUser=(authToken)=>{
     console.log('set');
     this.socket.emit('set-user',authToken);
   }
   public sendFriendRequest=(requestObj)=>{
     console.log('friend request socket');
     this.socket.emit('friend-request',requestObj);
   }
   public acceptFriendRequest=(requestObj)=>{
    console.log('accept friend request socket');
    this.socket.emit('accept-friend-request',requestObj);
  }

  

    

    

   //error handler
   private handleError(err:HttpErrorResponse){
     let erroMessage='';
     if(err.error instanceof Error){
      erroMessage='an error occur: '+err.error.message;
     }
     else{
       erroMessage='server return code: '+ err.status +' error message is :'+ err.error.message;
     }
     return Observable.throw(erroMessage);
   }

   

   
   public getAllUserList(userId):Observable<any>{
     console.log('getALL');
    console.log(userId);
     return this.http.get(this.url+'/api/v1/chat/unseen/user/list?userId='+userId+'&authToken='+Cookie.get('authToken'));
   }
   public exitSocket=()=>{
     this.socket.disconnect();
   }
}

